
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease,
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #000fdc;
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontrol .cart-move-enter {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
          transform: translate3D(24px, 0, 0) rotate(180deg);
}
.cartcontrol .cart-move-enter-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-move-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
          transform: translate3D(24px, 0, 0) rotate(180deg);
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}



